import {initBtnSettings} from "./button";
import {inputsFunc} from "./form";

document.addEventListener('DOMContentLoaded', () => {
  let block = document.querySelector('.opened-vacancies__catalog');
  if (block){
    fetch('vacancies.json')
      .then(response => response.json())
      .then(result => {
        const vacanciesArr = result.content

        initBtnSettings(document.querySelector('.opened-vacancies__catalog'))
        let actualFileList = [];
        let vacancies = document.querySelectorAll('.vacancy-js');
        let resumeBtns = document.querySelectorAll('.resume-btn-js')
        let arrayLinks = [...vacancies, ...resumeBtns];
        if (arrayLinks){
          arrayLinks.forEach(el => {
            el.addEventListener('click', (e) => {
              let index = [...arrayLinks].indexOf(e.target.closest('.card').querySelector('.vacancy-js'))
              let vacancyItem = vacanciesArr[index]

              document.querySelector('body').insertAdjacentHTML('afterbegin', `
                <div class="call-popup vacancies-popup">
                    <div class="call-popup__close">
                        <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path fill-rule="evenodd" clip-rule="evenodd" d="M10.4695 12.9004L1.01525 22.3546C0.454544 22.9153 0.454543 23.8244 1.01525 24.3851C1.57596 24.9458 2.48505 24.9458 3.04576 24.3851L12.5 14.9309L21.9542 24.3851C22.5149 24.9458 23.424 24.9458 23.9847 24.3851C24.5455 23.8244 24.5455 22.9153 23.9847 22.3546L14.5305 12.9004L23.9847 3.44615C24.5455 2.88544 24.5455 1.97635 23.9847 1.41564C23.424 0.854934 22.5149 0.854934 21.9542 1.41564L12.5 10.8699L3.04576 1.41564C2.48505 0.854935 1.57596 0.854934 1.01525 1.41564C0.454544 1.97635 0.454544 2.88544 1.01525 3.44614L10.4695 12.9004Z" fill="#AB2421"/>
                        </svg>
                    </div>
                    <h2 class="call-popup__title">Подробности</h2>
                    <h4 class="vacancies-popup__post">${vacancyItem.post}</h4>
                    <section class="vacancies-popup__about-company">
                        <div class="vacancies-popup__about-company-line">
                            <div class="vacancies-popup__about-company-param">Работодатель:</div>
                            <div class="vacancies-popup__about-company-value">${vacancyItem.company}</div>
                        </div>
                        <div class="vacancies-popup__about-company-line">
                            <div class="vacancies-popup__about-company-param">Место работы:</div>
                            <div class="vacancies-popup__about-company-value">${vacancyItem.workplace}</div>
                        </div>
                        <div class="vacancies-popup__about-company-line">
                            <div class="vacancies-popup__about-company-param">Регион:</div>
                            <div class="vacancies-popup__about-company-value">${vacancyItem.region}</div>
                        </div>
                        <div class="vacancies-popup__about-company-line">
                            <div class="vacancies-popup__about-company-param">Режим работы:</div>
                            <div class="vacancies-popup__about-company-value">${vacancyItem.workingMode}</div>
                        </div>
                        <div class="vacancies-popup__about-company-line">
                            <div class="vacancies-popup__about-company-param">Дата публикации:</div>
                            <div class="vacancies-popup__about-company-value">${vacancyItem.date}</div>
                    </section>
                    <div class="vacancies-popup__responsibilities">
                        <span class="vacancies-popup__responsibilities-title">Обязанности</span>
                        <ul></ul>
                    </div>
                    <div class="vacancies-popup__responsibilities vacancies-popup__normatives">
                        <span class="vacancies-popup__responsibilities-title">Требования</span>
                        <ul></ul>
                    </div>
                    <div class="vacancies-popup__responsibilities vacancies-popup__terms">
                        <span class="vacancies-popup__responsibilities-title">Условия</span>
                        <ul></ul>
                    </div>
                    <form class="form vacancies-popup__form" id="form-resume">
                        <h4 class="vacancies-popup__form-title">Отправить резюме</h4>
                        <div class="vacancies-popup__download-file">
                            <p class="vacancies-popup__form-comment">Если у Вас нет готового резюме, скачайте бланк резюме, заполните необходимые поля и отправьте его нам через форму</p>
                            <a href="images/banner.jpg" download class="vacancies-popup__form-link">
                                <div class="vacancies-popup__icon-download">
                                  <svg width="32" height="35" viewBox="0 0 32 35" fill="none" xmlns="http://www.w3.org/2000/svg">
                                      <path d="M3.97029 10.7984L4.1252 11.7864H4.1252L3.97029 10.7984ZM10.8974 3.94059L11.8848 4.09856V4.09856L10.8974 3.94059ZM27.5649 32.4244L26.981 31.6126L27.5649 32.4244ZM29.4085 30.5992L30.2147 31.1909L29.4085 30.5992ZM2.59152 30.5992L1.78533 31.1909L2.59152 30.5992ZM4.43512 32.4244L5.01904 31.6126H5.01904L4.43512 32.4244ZM4.43512 2.57561L5.01904 3.38742L4.43512 2.57561ZM2.59152 4.40077L1.78533 3.80912L2.59152 4.40077ZM27.5649 2.57561L26.981 3.38742L27.5649 2.57561ZM29.4085 4.40077L30.2147 3.80912L29.4085 4.40077ZM11 1.04773L10.9661 0.0483066L11 1.04773ZM1.04821 10.9L0.0488129 10.8654L1.04821 10.9ZM17 12.55C17 11.9977 16.5523 11.55 16 11.55C15.4477 11.55 15 11.9977 15 12.55H17ZM23.3734 21.1775C23.7641 20.7872 23.7645 20.154 23.3742 19.7633C22.9839 19.3725 22.3507 19.3722 21.96 19.7625L23.3734 21.1775ZM20.3369 22.7971L21.0436 23.5046L20.3369 22.7971ZM11.6631 22.7971L12.3698 22.0896L11.6631 22.7971ZM10.04 19.7625C9.6493 19.3722 9.01613 19.3725 8.62583 19.7633C8.23552 20.154 8.23588 20.7872 8.62662 21.1775L10.04 19.7625ZM16.4178 25.7237L16.543 26.7159H16.543L16.4178 25.7237ZM15.5822 25.7237L15.457 26.7159H15.457L15.5822 25.7237ZM4.1252 11.7864C8.11329 11.161 11.2507 8.06209 11.8848 4.09856L9.90996 3.78261C9.41454 6.8793 6.95883 9.31761 3.81538 9.8105L4.1252 11.7864ZM0 15.85V19.15H2V15.85H0ZM32 19.15V15.85H30V19.15H32ZM16 35C19.1027 35 21.5093 35.0013 23.4112 34.7973C25.3313 34.5914 26.8551 34.1668 28.1488 33.2362L26.981 31.6126C26.0841 32.2576 24.9503 32.6208 23.1979 32.8088C21.4274 32.9987 19.1468 33 16 33V35ZM30 19.15C30 22.266 29.9986 24.5219 29.807 26.2727C29.6175 28.0042 29.2518 29.1225 28.6023 30.0076L30.2147 31.1909C31.1567 29.9073 31.5867 28.3945 31.7951 26.4903C32.0014 24.6054 32 22.221 32 19.15H30ZM28.1488 33.2362C28.9412 32.6663 29.6384 31.9761 30.2147 31.1909L28.6023 30.0076C28.1506 30.6231 27.6035 31.1648 26.981 31.6126L28.1488 33.2362ZM0 19.15C0 22.221 -0.00140047 24.6054 0.204875 26.4903C0.413264 28.3945 0.843318 29.9073 1.78533 31.1909L3.39772 30.0076C2.7482 29.1225 2.3825 28.0042 2.193 26.2727C2.0014 24.5219 2 22.266 2 19.15H0ZM16 33C12.8532 33 10.5726 32.9987 8.80208 32.8088C7.04972 32.6208 5.91586 32.2576 5.01904 31.6126L3.85121 33.2362C5.14493 34.1668 6.66873 34.5914 8.58879 34.7973C10.4907 35.0013 12.8973 35 16 35V33ZM1.78533 31.1909C2.36161 31.9761 3.05884 32.6663 3.85121 33.2362L5.01904 31.6126C4.3965 31.1648 3.84944 30.6231 3.39772 30.0076L1.78533 31.1909ZM3.85121 1.7638C3.05884 2.33373 2.36161 3.02388 1.78533 3.80912L3.39772 4.99242C3.84944 4.3769 4.3965 3.8352 5.01904 3.38742L3.85121 1.7638ZM16 2C19.1468 2 21.4274 2.00135 23.1979 2.19125C24.9503 2.37921 26.0841 2.74236 26.981 3.38742L28.1488 1.7638C26.8551 0.833248 25.3313 0.408599 23.4112 0.202654C21.5093 -0.00134563 19.1027 0 16 0V2ZM32 15.85C32 12.779 32.0014 10.3946 31.7951 8.50969C31.5867 6.60551 31.1567 5.09272 30.2147 3.80912L28.6023 4.99242C29.2518 5.87746 29.6175 6.99576 29.807 8.72727C29.9986 10.4781 30 12.734 30 15.85H32ZM26.981 3.38742C27.6035 3.8352 28.1506 4.3769 28.6023 4.99242L30.2147 3.80912C29.6384 3.02388 28.9412 2.33373 28.1488 1.7638L26.981 3.38742ZM16 0C14.0573 0 12.3939 -0.000122786 10.9661 0.0483066L11.0339 2.04716C12.4206 2.00012 14.0463 2 16 2V0ZM10.9661 0.0483066C7.84755 0.154084 5.62341 0.489094 3.85121 1.7638L5.01904 3.38742C6.26219 2.49325 7.9385 2.15215 11.0339 2.04716L10.9661 0.0483066ZM10 1.04772C9.99999 2.58584 9.99582 3.24594 9.90996 3.78261L11.8848 4.09856C12.002 3.36602 12 2.51184 12 1.04774L10 1.04772ZM2 15.85C2 13.9157 2.00013 12.3068 2.04762 10.9346L0.0488129 10.8654C-0.000127077 12.2796 0 13.9269 0 15.85H2ZM2.04762 10.9346C2.15361 7.87182 2.49774 6.21875 3.39772 4.99242L1.78533 3.80912C0.494532 5.56798 0.155716 7.77637 0.0488129 10.8654L2.04762 10.9346ZM1.04822 11.9C2.52819 11.9 3.38812 11.9019 4.1252 11.7864L3.81538 9.8105C3.27042 9.89595 2.60078 9.89999 1.04821 9.9L1.04822 11.9ZM15 12.55V24.43H17V12.55H15ZM21.96 19.7625L19.6302 22.0896L21.0436 23.5046L23.3734 21.1775L21.96 19.7625ZM12.3698 22.0896L10.04 19.7625L8.62662 21.1775L10.9564 23.5046L12.3698 22.0896ZM19.6302 22.0896C18.6847 23.0341 18.0341 23.6818 17.4829 24.12C16.9478 24.5455 16.603 24.6924 16.2926 24.7316L16.543 26.7159C17.3648 26.6122 18.0506 26.2238 18.7276 25.6855C19.3884 25.1602 20.1313 24.416 21.0436 23.5046L19.6302 22.0896ZM10.9564 23.5046C11.8687 24.416 12.6116 25.1602 13.2724 25.6855C13.9494 26.2238 14.6352 26.6122 15.457 26.7159L15.7074 24.7316C15.397 24.6924 15.0522 24.5455 14.5171 24.12C13.9659 23.6818 13.3153 23.0341 12.3698 22.0896L10.9564 23.5046ZM16.2926 24.7316C16.1954 24.7439 16.0977 24.75 16 24.75V26.75C16.1814 26.75 16.3627 26.7386 16.543 26.7159L16.2926 24.7316ZM16 24.75C15.9023 24.75 15.8046 24.7439 15.7074 24.7316L15.457 26.7159C15.6373 26.7386 15.8187 26.75 16 26.75V24.75ZM15 24.43V25.75H17V24.43H15Z" fill="#AB2421"/>
                                  </svg>
                                </div>
                                <div>Бланк резюме <span class="vacancies-popup__size-file">0.23 MB (.doc)</span></div>
                            </a>
                        </div>
                        <div class="vacancies-popup__inputs">
                           <div class="form__item">
                             <input class="form__input call-popup__input" type="text" id="call-popup-name" name="name"/>
                             <label class="form__input-label" for="call-popup-name">Введите ваше имя</label>
                           </div>
                           <div class="form__item">
                             <input class="form__input call-popup__input" type="text" data-mask="phone" id="call-popup-phone" name="phone"/>
                             <label class="form__input-label" for="call-popup-phone">Ваш телефон</label>
                           </div>
                        </div>
                        <div class="form__files"></div>
                        <div class="form__button-files-wrp">
                            <label for="files" class="btn btn__white form__files-button">
                              <input class="inputFiles-js" type="file" id="files" accept=".doc, .docx, .rtf, .pdf" multiple>Выберите файл</input>
                            </label>

                            <div>
                                <div class="form__files-comment">Прикрепите еще файл с резюме</div>
                                <div class="form__files-formats">Файл с резюме в формате: doc, docx, rtf, pdf</div>
                            </div>
                        </div>

                        <button class="btn btn__red form__btn call-popup__btn vacancies-popup__submit-btn" type="submit">Отправить</button>

                       <div class="call-popup__comments">
                         <div>Пожалуйста, заполните все поля - так нам будет легче связаться с вами</div>
                         <div> Нажимая на кнопку «Отправить», я даю согласие на <a class="form__personal-data" href="#">обработку персональных данных</a></div>
                       </div>
                    </form>
                </div>
`);
              if (vacancyItem.responsibilities){
                vacancyItem.responsibilities.forEach(el => {
                  let list = document.querySelector('.vacancies-popup__responsibilities').querySelector('ul')
                  list.insertAdjacentHTML('beforeend', `
                      <li>${el.item}</li>
                  `)
                })
              }
              if (vacancyItem.normatives){
                vacancyItem.normatives.forEach(el => {
                  let list = document.querySelector('.vacancies-popup__normatives').querySelector('ul')
                  list.insertAdjacentHTML('beforeend', `
                      <li>${el.item}</li>
                  `)
                })
              }
              if (vacancyItem.terms){
                vacancyItem.terms.forEach(el => {
                  let list = document.querySelector('.vacancies-popup__terms').querySelector('ul')
                  list.insertAdjacentHTML('beforeend', `
                      <li>${el.item}</li>
                  `)
                })
              }

              let inputFile = document.getElementById('files');
              inputFile.addEventListener('change', () => {
                let files = [...document.getElementById('files').files];
                actualFileList.push(...files);
                document.querySelector('.form__files').innerHTML = ``
                actualFileList.forEach(file => {
                  document.querySelector('.form__files').insertAdjacentHTML('beforeend', `
                      <div class="vacancies-popup__form-files">
                        <div class="vacancies-popup__file-img">
                            <img src="images/svg/document-icon.svg" alt="document-icon">
                            <span class="vacancies-popup__file-format">${file.name.split('.')[1]}</span>
                        </div>
                        <div class="vacancies-popup__form-file">${file.name.split('.')[0]}</div>
                        <button class="vacancies-popup__file-img vacancies-popup__delete-file">
                          <svg width="17" height="22" viewBox="0 0 17 22" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <rect x="0.5" y="4" width="16" height="4" rx="2" stroke="#999999"/>
                            <path d="M2.5 8H14.5V19.5C14.5 20.3284 13.8284 21 13 21H4C3.17157 21 2.5 20.3284 2.5 19.5V8Z" stroke="#999999"/>
                            <path d="M5.5 18.243C5.77619 18.243 6 18.0297 6 17.7666V12.4266C6 12.1634 5.77619 11.9502 5.5 11.9502C5.22381 11.9502 5 12.1634 5 12.4266V17.7666C5 18.0297 5.22381 18.243 5.5 18.243Z" fill="#999999"/>
                            <path d="M8.5 18.243C8.77619 18.243 9 18.0297 9 17.7666V12.4266C9 12.1634 8.77619 11.9502 8.5 11.9502C8.22381 11.9502 8 12.1634 8 12.4266V17.7666C8.00476 18.0297 8.22857 18.243 8.5 18.243Z" fill="#999999"/>
                            <path d="M11.5 18.243C11.7762 18.243 12 18.0297 12 17.7666V12.4266C12 12.1634 11.7762 11.9502 11.5 11.9502C11.2238 11.9502 11 12.1634 11 12.4266V17.7666C11 18.0297 11.2238 18.243 11.5 18.243Z" fill="#999999"/>
                            <path d="M11.2716 2.35195C11.3581 2.56077 11.4205 2.77819 11.458 3H8.5L5.54196 3C5.57945 2.77819 5.64187 2.56077 5.72836 2.35195C5.87913 1.98797 6.1001 1.65726 6.37868 1.37868C6.65726 1.1001 6.98797 0.879125 7.35195 0.728361C7.71593 0.577597 8.10603 0.5 8.5 0.5C8.89397 0.5 9.28407 0.577597 9.64805 0.728361C10.012 0.879125 10.3427 1.1001 10.6213 1.37868C10.8999 1.65726 11.1209 1.98797 11.2716 2.35195Z" stroke="#999999"/>
                          </svg>
                        </button>
                      </div>
                  `)
                })

                const predDeleteFunc = () => {
                  if (!document.querySelector('.vacancies-popup__form-files-title') && actualFileList.length>0){
                    document.querySelector('.form__files').insertAdjacentHTML('beforebegin', `
                      <div class="vacancies-popup__form-files-title">Прикрепленный файл</div>
                  `);
                  }
                  if (actualFileList){
                    let btnsDelete = document.querySelectorAll('.vacancies-popup__delete-file');
                    if (btnsDelete){
                      btnsDelete.forEach(btn => {
                        btn.addEventListener('click', (e) => {
                          let index = [...btnsDelete].indexOf(btn)
                          deleteFunc(index);
                          if (actualFileList){
                            predDeleteFunc()
                          }
                        });
                      })
                    }
                  }
                }
                predDeleteFunc()
              })

              let toCallPopup = document.querySelector('.call-popup')
              setTimeout(() => {
                toCallPopup.classList.add('call-popup__opened');
                toCallPopup.insertAdjacentHTML('afterend',`<div class="dark-block"></div>`);
                document.body.style.overflow = "hidden"
              },100)

              initBtnSettings(toCallPopup);
              inputsFunc(toCallPopup)

              let btnClose = toCallPopup.querySelector('.call-popup__close');
              let closePopupCall = () => {
                toCallPopup.classList.add('call-popup__closed')
                setTimeout(() => {
                  toCallPopup.remove();
                  document.querySelector('.dark-block')?.remove();
                  document.body.style.overflow = "auto"
                },100)
              }

              btnClose.addEventListener('click',() => {
                closePopupCall()
              })

              document.addEventListener('click', (e) => {
                if (e.target === document.querySelector('.dark-block')){
                  closePopupCall()
                }
              })

              const deleteFunc = (index) => {
                actualFileList.splice(index,1)
                document.querySelector('.form__files').innerHTML = "";
                actualFileList.forEach(file => {
                  document.querySelector('.form__files').insertAdjacentHTML('beforeend', `
                      <div class="vacancies-popup__form-files">
                        <div class="vacancies-popup__file-img">
                            <img src="images/svg/document-icon.svg" alt="document-icon">
                            <span class="vacancies-popup__file-format">${file.name.split('.')[1]}</span>
                        </div>
                        <div class="vacancies-popup__form-file">${file.name.split('.')[0]}</div>
                        <button class="vacancies-popup__file-img vacancies-popup__delete-file">
                          <svg width="17" height="22" viewBox="0 0 17 22" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <rect x="0.5" y="4" width="16" height="4" rx="2" stroke="#999999"/>
                            <path d="M2.5 8H14.5V19.5C14.5 20.3284 13.8284 21 13 21H4C3.17157 21 2.5 20.3284 2.5 19.5V8Z" stroke="#999999"/>
                            <path d="M5.5 18.243C5.77619 18.243 6 18.0297 6 17.7666V12.4266C6 12.1634 5.77619 11.9502 5.5 11.9502C5.22381 11.9502 5 12.1634 5 12.4266V17.7666C5 18.0297 5.22381 18.243 5.5 18.243Z" fill="#999999"/>
                            <path d="M8.5 18.243C8.77619 18.243 9 18.0297 9 17.7666V12.4266C9 12.1634 8.77619 11.9502 8.5 11.9502C8.22381 11.9502 8 12.1634 8 12.4266V17.7666C8.00476 18.0297 8.22857 18.243 8.5 18.243Z" fill="#999999"/>
                            <path d="M11.5 18.243C11.7762 18.243 12 18.0297 12 17.7666V12.4266C12 12.1634 11.7762 11.9502 11.5 11.9502C11.2238 11.9502 11 12.1634 11 12.4266V17.7666C11 18.0297 11.2238 18.243 11.5 18.243Z" fill="#999999"/>
                            <path d="M11.2716 2.35195C11.3581 2.56077 11.4205 2.77819 11.458 3H8.5L5.54196 3C5.57945 2.77819 5.64187 2.56077 5.72836 2.35195C5.87913 1.98797 6.1001 1.65726 6.37868 1.37868C6.65726 1.1001 6.98797 0.879125 7.35195 0.728361C7.71593 0.577597 8.10603 0.5 8.5 0.5C8.89397 0.5 9.28407 0.577597 9.64805 0.728361C10.012 0.879125 10.3427 1.1001 10.6213 1.37868C10.8999 1.65726 11.1209 1.98797 11.2716 2.35195Z" stroke="#999999"/>
                          </svg>
                        </button>
                      </div>
                  `)
                })
                if (actualFileList.length === 0){
                  let title = document.querySelector('.vacancies-popup__form-files-title');
                  if (title) {
                    title.remove()
                  }
                }
              }
              if (e.target.classList.contains('resume-btn-js')){
                setTimeout(() => {
                  const element = document.getElementById('form-resume');
                  element.scrollIntoView()
                },500)
              }
            })
          })
        }
      })
  }
})



