document.addEventListener('DOMContentLoaded', () => {
  let tabs = document.querySelectorAll('.tabs')
  if (tabs){
    tabs.forEach(el => {
      let inputs = el.querySelectorAll('input');
      inputs.forEach((input,index) => {
        input.addEventListener('click', (e) => {
          if (document.querySelector('.tabs__catalog').querySelector('.tabs__item--checked')){
            document.querySelector('.tabs__item--checked').classList.remove('tabs__item--checked')
          }
          e.target.parentElement.querySelector('label').classList.toggle('tabs__item--checked')
          document.querySelector('.tabs__checked').classList.remove('tabs__checked')
          let foundArr = e.target.closest('.tabs').querySelectorAll('.tabs__content');
          foundArr[index].classList.add('tabs__checked')
        })

      })

      //Если вместо табов dropdown-блоки
      let items = document.querySelectorAll('.dropdown__params-item');
      items.forEach((item, index) => {
        item.addEventListener('click', (e) => {
          document.querySelector('.tabs__checked').classList.remove('tabs__checked')
          let foundArr = e.target.closest('.tabs').querySelectorAll('.tabs__content');
          foundArr[index].classList.add('tabs__checked')
        })
      })
    })
  }
})
