import Swiper from 'swiper';

document.addEventListener('DOMContentLoaded', () => {
  const sliders = document.querySelectorAll('.slider-container');
  if (sliders.length !== 0){
    [...sliders].forEach((slider) => {
      const container = slider.querySelector('.swiper-container');
      const slides = slider.querySelectorAll('.swiper-slide');
      let navigation = slider.querySelector('.navigation');
      const sliderSettings = () => {
        if(container.classList.contains('banner__slider')){
          let activeSlide = container.swiper.slides[container.swiper.activeIndex];
          if (activeSlide.getAttribute('data-theme')){
            document.body.classList.add('light-theme')
          } else {
            document.body.classList.remove('light-theme')
          }
          let activeTitle = activeSlide.querySelector('.banner__fixed-title')
          if (activeTitle){
            let breadcrumbs = document.querySelector('.banner__fixed-block');
            let breadcrumbsHeight = breadcrumbs.getBoundingClientRect().height
            breadcrumbs.style.bottom = 'initial';
            setTimeout(()=>{
              let calc = activeTitle?.getBoundingClientRect()
              if (window.innerWidth > 600){
                breadcrumbs.style.top = `${window.scrollY + calc.top - breadcrumbsHeight - 27}px`
              } else {
                breadcrumbs.style.top = `${window.scrollY + calc.top - breadcrumbsHeight - 40}px`
              }
              breadcrumbs.style.opacity = "1";
            }, 1)
          }

        }
      }
      if (slides.length < 2){
        navigation.style.display = "none"
      }
      if (slides.length > 0){
        const swiper = new Swiper(container, {
          slidesPerView: 1,
          loop: slides.length > 1 && true,
          noSwipingClass:'banner__main',
          autoplay: true,
          pagination: {
            el: slider.querySelector('.banner__pagination'),
            clickable: true,
          },
          navigation: {
            nextEl: '.banner__next',
            prevEl: '.banner__prev',
          },
          on: {
            init:function (){
              sliderSettings()
            },
            slideChange: function () {
              sliderSettings()
            },
          },
        });
      }
        else {
          slider.querySelector('.navigation').style.display = "none"
          let activeSlide = document.querySelector('.banner__slider').querySelector('.swiper-slide');
          let activeTitle = activeSlide.querySelector('.banner__fixed-title');
          if (activeTitle){
            let calc = activeTitle.getBoundingClientRect()
            let breadcrumbs = document.querySelector('.banner__fixed-block');
            let breadcrumbsHeight = breadcrumbs.getBoundingClientRect().height
            if (window.innerWidth > 600){
              breadcrumbs.style.top = `${calc.top - breadcrumbsHeight - 27}px`
            } else {
              breadcrumbs.style.top = `${calc.top - breadcrumbsHeight - 40}px`
            }
          }
      }
    })
  }
});

document.addEventListener('DOMContentLoaded', () => {
  const sliders = document.querySelectorAll('.news__slider');
  if (sliders.length > 0){
    [...sliders].forEach((slider) => {
      let slidesCount = slider.querySelectorAll('.swiper-slide').length;
      const container = slider.querySelector('.swiper-container');
      let windowWidth = window.innerWidth
      let navigation = slider.closest('.news').querySelector('.navigation')
      if (slidesCount < 2 || (windowWidth > 1400 && slidesCount === 3) || (windowWidth > 600 && slidesCount < 3)){
        navigation.style.display = "none"
      }
      if (slidesCount > 1){
        new Swiper(container, {
          autoplay: true,
          loop:slidesCount > 3 && true,
          navigation: {
            nextEl: container.closest('.news').querySelector('.banner__next'),
            prevEl: container.closest('.news').querySelector('.banner__prev'),
          },
          breakpoints: {
            360: {
              slidesPerView: 1,
              spaceBetween: 30,
              centeredSlides: true
            },
            601: {
              slidesPerView: 1.85,
              spaceBetween: 30
            },
            781: {
              slidesPerView: 1.9,
              spaceBetween: 30
            },
            1400: {
              slidesPerView: 'auto',
              spaceBetween: 30
            }
          }
        });
      }
    })
  }
});

document.addEventListener('DOMContentLoaded', () => {
  const galleryThumbs = new Swiper(".slider-with-previews__pagination", {
    spaceBetween: 0,
    slidesPerView: 10,
    freeMode: true,
    autoplay: true,
    breakpoints: {
      360: {
        slidesPerView: 7,
      },
      781: {
        slidesPerView: 9,
      },
      1025: {
        slidesPerView: 8,
      },
      1281: {
        slidesPerView: 5,
      },
      1401:{
        slidesPerView: 5,
      },
      1601: {
        slidesPerView: 10,
      }
    }

  });


  const sliders = document.querySelectorAll('.news-more-block__slider');
  if (sliders.length > 0){
    [...sliders].forEach((slider) => {
      const container = slider.querySelector('.swiper-container');
      new Swiper(container, {
        spaceBetween: 30,
        autoplay: true,
        loopedSlides: 5, //looped slides should be the same
        navigation: {
          nextEl: container.querySelector('.banner__next'),
          prevEl: container.querySelector('.banner__prev'),
        },
        pagination: {
          el: slider.querySelector('.swiper-pagination'),
          clickable: true,
          type: 'custom',
          bulletClass:'swiper-pagination-img'
        },
        thumbs: {
          swiper: galleryThumbs
        }
      });
    })
  }
});


document.addEventListener('DOMContentLoaded', () => {
  const sliders = document.querySelectorAll('.manager-block__slider');
  if (sliders.length > 0){
    [...sliders].forEach((slider) => {
      const container = slider.querySelector('.swiper-container');
      const slidesCount = slider.querySelectorAll('.swiper-slide').length;
      const bottomBlock = slider.querySelector('.manager-block__slider-info');
      if (slidesCount < 2){
        bottomBlock.style.opacity = '0'
      }
      let swiper = new Swiper(container, {
        slidesPerView: 1,
        spaceBetween: 0,
        autoplay: true,
        pagination: {
          el: slider.querySelector('.swiper-pagination'),
          clickable: true,
          dynamicBullets: true,
          dynamicMainBullets: 1
        },
        navigation: {
          nextEl: slider.querySelector('.button-next'),
          prevEl: slider.querySelector('.button-prev'),
        },
      });
      let dropdownBlocks = slider.querySelectorAll('.title-accordion-js')
      dropdownBlocks.forEach(el => {
        el.addEventListener('click', () => {
          if (swiper.autoplay.running){
            swiper.autoplay.stop()
          } else {
            swiper.autoplay.start()
          }
        })
      });
      let mobilePopupBtns = slider.querySelectorAll('.manager-block__info-wrp-header');
      mobilePopupBtns.forEach(el => {
        el.addEventListener('click', () => {
          if (swiper.autoplay.running){
            swiper.autoplay.stop()
          } else {
            swiper.autoplay.start()
          }
        })
      })
    })
  }
});

