document.addEventListener('DOMContentLoaded', () => {
  let accordions = document.querySelectorAll('.accordion-js');
  if (accordions){
    accordions.forEach(el => {
      el.addEventListener('click', () => {
        const dropdownBlock = el.querySelector('.dropdown__params-wrp');
        //если position:absolute
        if (dropdownBlock){
          dropdownBlock.classList.toggle('dropdown__active');
          if (dropdownBlock.style.maxHeight) {
            dropdownBlock.style.maxHeight = null;
            setTimeout(() => {
              dropdownBlock.style.padding = '0';
            },200)
          } else {
            dropdownBlock.style.padding = '0 12px';
            setTimeout(() => {
              dropdownBlock.style.maxHeight = dropdownBlock.scrollHeight + "px";
            },1)
          }
        }


        //если не position:absolute
        const dropdownBlockRelative = el.querySelector('.dropdown-block-js');
        if (dropdownBlockRelative && window.innerWidth>1200){
          dropdownBlockRelative.classList.toggle('dropdown__active');
          if (dropdownBlockRelative.style.maxHeight) {
            dropdownBlockRelative.style.maxHeight = null;
            setTimeout(() => {
              dropdownBlockRelative.parentElement.querySelector('.accordion-icon-js').classList.remove('accordion-icon-active')
              dropdownBlockRelative.parentElement.querySelector('.title-accordion-js').classList.toggle('dashed')
              dropdownBlockRelative.style.paddingTop = '0';
              dropdownBlockRelative.style.paddingBottom = '0';
            },200)
          } else {
            dropdownBlockRelative.parentElement.querySelector('.title-accordion-js').classList.toggle('dashed')
            dropdownBlockRelative.style.maxHeight = dropdownBlockRelative.scrollHeight + "px";
            dropdownBlockRelative.style.paddingTop = '26px';
            dropdownBlockRelative.style.paddingBottom = '26px';
            dropdownBlockRelative.parentElement.querySelector('.accordion-icon-js').classList.add('accordion-icon-active')
          }
        }


      })
    })
  }


  document.addEventListener('click', (e) => {
    if (!e.target.classList.contains('tabs__dropdown') && !e.target.classList.contains('dropdown__header-content')){
      if (document.querySelector('.dropdown__params-wrp')){
        document.querySelector('.dropdown__params-wrp').style.maxHeight = null
      }
    }
  })

  const dropdownBlocks = document.querySelectorAll('.dropdown__params-item');
  if (dropdownBlocks){
    dropdownBlocks.forEach((block) => {
      block.addEventListener('click', () => {
        document.querySelector('.dropdown__header-content').innerText = block.textContent
      })
    })
  }
})
