let checkboxes = document.querySelectorAll('.profy__checkbox');
if (checkboxes){
  checkboxes.forEach(el => {
    el.addEventListener('change',() => {
      if (el.checked){
        el.parentElement.querySelector('.profy__label-name').insertAdjacentHTML('beforeend','<span class="profy__label-check"></span>')
      } else {
        el.parentElement.querySelector('.profy__label-name').querySelector('.profy__label-check').remove()
      }
    })
  })
}

