document.addEventListener('DOMContentLoaded', () => {
  const init = () => {
    let arrayMarkers = [];
    arrayMarkers.push([57.950701, 56.179254])
    arrayMarkers.push([55.00202076433394,82.95604349999992])
    let currentMarker = 0;
    let arrayLinks = [];
    arrayLinks.push('https://yandex.ru/maps/50/perm/?from=api-maps&ll=56.222856%2C57.954534&mode=routes&origin=jsapi_2_1_79&pt=82.95604349999992%2C55.00202076433394&rtext=~57.950701%2C56.179254&rtt=auto&ruri=~ymapsbm1%3A%2F%2Fgeo%3Fdata%3DCgoyMTE2MjE3ODY5EkzQoNC%2B0YHRgdC40Y8sINCf0LXRgNC80YwsINGD0LvQuNGG0LAg0JrQvtGB0LzQvtC90LDQstGC0LAg0JvQtdC%2B0L3QvtCy0LAsIDc1IgoNjrdgQhWFzWdC&z=12')
    arrayLinks.push('https://yandex.ru/maps/65/novosibirsk/?from=api-maps&ll=82.955529%2C55.023131&mode=routes&origin=jsapi_2_1_79&pt=56.179254%2C57.950701&rtext=~55.002021%2C82.956043&rtt=auto&ruri=~&z=13')
    let currentLink = 0;

    var myMap = new ymaps.Map('map', {
        center: [57.950701, 56.179254],
        zoom: 13,
        controls:[]
      }, {
        searchControlProvider: 'yandex#search'
      }),

      // Создаём макет содержимого.
      MyIconContentLayout = ymaps.templateLayoutFactory.createClass(
        '<div style="color: #FFFFFF; font-weight: bold;">$[properties.iconContent]</div>'
      ),



      myPlacemarkWithContent = new ymaps.Placemark([57.950701, 56.179254], {
      // hintContent: 'ООО "Импортсервис"',
      // balloonContent: 'ООО "Импортсервис"',
    }, {
      // Опции.
      // Необходимо указать данный тип макета.
      iconLayout: 'default#image',
      // Своё изображение иконки метки.
      iconImageHref: '/dist/images/svg/yandex-gps-icon.svg',
      // Размеры метки.
      iconImageSize: [48, 48],
      // Смещение левого верхнего угла иконки относительно
      // её "ножки" (точки привязки).
      iconImageOffset: [-24, -24],
      // Смещение слоя с содержимым относительно слоя с картинкой.
      iconContentOffset: [15, 15],
      // Макет содержимого.
      iconContentLayout: MyIconContentLayout
    });
    myPlacemarkWithContent2 = new ymaps.Placemark([55.00202076433394,82.95604349999992], {
      // hintContent: 'ООО "Импортсервис"',
      // balloonContent: 'ООО "Импортсервис"',
    }, {
      // Опции.
      // Необходимо указать данный тип макета.
      iconLayout: 'default#image',
      // Своё изображение иконки метки.
      iconImageHref: '/dist/images/svg/yandex-gps-icon.svg',
      // Размеры метки.
      iconImageSize: [48, 48],
      // Смещение левого верхнего угла иконки относительно
      // её "ножки" (точки привязки).
      iconImageOffset: [-24, -24],
      // Смещение слоя с содержимым относительно слоя с картинкой.
      iconContentOffset: [15, 15],
      // Макет содержимого.
      iconContentLayout: MyIconContentLayout
    });
    myMap.geoObjects
      .add(myPlacemarkWithContent)
      .add(myPlacemarkWithContent2)

      myMap.behaviors. disable('scrollZoom');
      myMap.behaviors.disable('drag');
      let map = document.querySelector('.banner--map');
      map.addEventListener('click', () => {
        map.classList.toggle('drag');
        if (map.classList.contains('drag')){
          myMap.behaviors.enable('drag')
          myMap.behaviors.enable('scrollZoom');
        } else {
          myMap.behaviors.disable('scrollZoom');
        }
      })

    //вперед
    document.querySelector('.banner--map').querySelector('.banner__next').addEventListener('click', () => {
      let mapPos;
      if(currentMarker === (arrayMarkers.length - 1) ){
        currentMarker = 0;
      }else{
        currentMarker++;
      }
      document.querySelector('.banner__map-btn').setAttribute('href', arrayLinks[currentMarker])
      mapPos = arrayMarkers[currentMarker];
      myMap.panTo(mapPos, {
        duration: 2500,
      });
    })

    //назад
    document.querySelector('.banner--map').querySelector('.banner__prev').addEventListener('click', () => {
      let mapPos;
      if(currentMarker === 0 ){
        currentMarker = (arrayMarkers.length - 1);
      }else{
        currentMarker--;
      }
      document.querySelector('.banner__map-btn').setAttribute('href', arrayLinks[currentMarker])
      mapPos = arrayMarkers[currentMarker];
      myMap.panTo(mapPos, {
        duration: 2500,
      });
    })
  }
  if (document.getElementById('map')){
    document.body.classList.add('light-theme')
    ymaps.ready(init);
  }


})


