document.addEventListener('DOMContentLoaded', () => {
  let texts = document.querySelectorAll('.history__item-text');
  if (texts){
    texts.forEach(text => {
      if (window.innerWidth > 1280){
        text.addEventListener('mouseover', () => {
          text.querySelector('.history__item-text--preview').classList.add('history__item-text--hidden');
          text.querySelector('.history__item-text--detail').classList.add('history__item-text--show');
        })
        text.addEventListener('mouseout', () => {
          text.querySelector('.history__item-text--preview').classList.remove('history__item-text--hidden');
          text.querySelector('.history__item-text--detail').classList.remove('history__item-text--show');
        })
      } else {
        text.addEventListener('click', (e) => {
          if (!e.target.closest('svg')){
            text.querySelector('.history__item-text--preview').classList.toggle('history__item-text--hidden');
            text.querySelector('.history__item-text--detail').classList.toggle('history__item-text--show');
          }
        })
        let closeBtn = text.querySelector('.history__item-close-btn');
        if (closeBtn){
          closeBtn.addEventListener('click', () => {
            text.querySelector('.history__item-text--preview').classList.remove('history__item-text--hidden');
            text.querySelector('.history__item-text--detail').classList.remove('history__item-text--show');
          })
        }
      }
    })
  }
})
