  document.addEventListener('DOMContentLoaded', () => {
    let btnPopups = document.querySelector('.page-with-manager__manager-block-mobile')?.querySelectorAll('.manager-block__info-wrp-header');
    let managerPopups = document.querySelector('.page-with-manager__manager-block-mobile')?.querySelectorAll('.mobile-popup-js')
    if (btnPopups && managerPopups) {
      btnPopups.forEach((btnPopup, index) => {
        btnPopup.addEventListener('click', () => {
          if (window.innerWidth <= 1200) {
            managerPopups[index]?.classList.add('manager-block__mobile-popup--opened');
            if (window.innerWidth > 750){
              let activeImg = document.querySelector('.page-with-manager__manager-block-mobile')?.querySelector('.swiper-slide-active'). querySelector('.manager-block__img');
              managerPopups[index].style.width = `calc(100% - ${activeImg.getBoundingClientRect().width}px)`
            }
          }
          let btnClose = managerPopups[index]?.querySelector('.close-btn-js');
          let closePopup = () => {
            managerPopups[index]?.classList.add('manager-block__mobile-popup--closed')
            setTimeout(() => {
              managerPopups[index]?.classList.remove('manager-block__mobile-popup--closed');
              managerPopups[index]?.classList.remove('manager-block__mobile-popup--opened');
            },100)
          }
          if (btnClose){
            btnClose.addEventListener('click',() => {
              closePopup()
            })
          }
        })


      })

    }
  })
