document.addEventListener('DOMContentLoaded', () => {
  let contents = document.querySelectorAll('.content');
  if (contents){
    contents.forEach(content => {
      let tables = content.querySelectorAll('table');
      if (tables){
        tables.forEach(table => {
          if (table && window.innerWidth <= 950){
            let tbodyTrs = table.querySelector('tbody').querySelectorAll('tr');
            let theadTds = table.querySelector('thead').querySelector('tr').querySelectorAll('td');
            tbodyTrs.forEach((el,index) => {
              theadTds[index].style.height = el.getBoundingClientRect().height + 'px'
            })
          }
        })
      }
    })
  }
})
