import {initBtnSettings} from "./button";
import {inputsFunc} from "./form";

const changeWithBlock = () => {
  let subcategories = document.querySelectorAll('.product-popup__subcategories');
  if (!subcategories.length){
    document.querySelectorAll('.product-popup__category-item').forEach(el => {
      el.style.width = "100%"
    })
  }
}

const dropdownFunc = () => {
  let accordions = document.querySelectorAll('.product-popup__dropdown-icon')
  if (accordions){
    accordions.forEach(el => {
      el.addEventListener('click', (e) => {
        let container = el.closest('.product-popup__subcategory-container')
        el.classList.toggle('product-popup__dropdown-icon--active');
        container.classList.toggle('active');
        let dropdownMenu = container.querySelector('.product-popup__products')
        if (dropdownMenu.style.maxHeight){
          dropdownMenu.style.maxHeight = null;
          dropdownMenu.style.paddingTop = "0"
        } else {
          dropdownMenu.style.maxHeight = dropdownMenu.scrollHeight + "px";
          dropdownMenu.style.paddingTop = "20px"
        }
        if (container.parentElement.querySelectorAll('.active').length > 1){
          container.parentElement.querySelectorAll('.active').forEach(item => {
            if (item !== el){
              item.classList.remove('active');
              item.querySelector('.product-popup__products').style.maxHeight = null;
              item.querySelector('.product-popup__products').style.paddingTop = '0';
              if (item.querySelector('.product-popup__dropdown-icon--active')){
                item.querySelector('.product-popup__dropdown-icon--active').classList.remove('product-popup__dropdown-icon--active');
              }
              dropdownMenu.style.maxHeight = dropdownMenu.scrollHeight + "px";
              dropdownMenu.style.paddingTop = "20px"
              el.closest('.product-popup__subcategory-container').classList.add('active')
              el.classList.add('product-popup__dropdown-icon--active');
            }
          })
        }
      })
    })
  }
}

document.addEventListener('DOMContentLoaded', () => {
  let products = document.querySelectorAll('.production__link');
  if (products){
    fetch('products.json')
      .then(response => response.json())
      .then(result => {
        const productsArray =  result.content
        let productsArr = document.querySelectorAll('.product-js');
        if (productsArr){
          productsArr.forEach(element => {
            element.addEventListener('click', () => {
              let idElement = element.getAttribute('data-id');


              let foundElement = productsArray.find(el => Number(el.id) === Number(idElement) )

              document.querySelector('body').insertAdjacentHTML('afterbegin', `
                <div class="call-popup product-popup">
                    <button class="mobile-menu__close product-popup__close">
                      <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd" clip-rule="evenodd" d="M10.4695 12.9004L1.01525 22.3546C0.454544 22.9153 0.454543 23.8244 1.01525 24.3851C1.57596 24.9458 2.48505 24.9458 3.04576 24.3851L12.5 14.9309L21.9542 24.3851C22.5149 24.9458 23.424 24.9458 23.9847 24.3851C24.5455 23.8244 24.5455 22.9153 23.9847 22.3546L14.5305 12.9004L23.9847 3.44615C24.5455 2.88544 24.5455 1.97635 23.9847 1.41564C23.424 0.854934 22.5149 0.854934 21.9542 1.41564L12.5 10.8699L3.04576 1.41564C2.48505 0.854935 1.57596 0.854934 1.01525 1.41564C0.454544 1.97635 0.454544 2.88544 1.01525 3.44614L10.4695 12.9004Z" fill="white"/>
                      </svg>
                    </button>
                    <div class="product-popup__container">
                      <div class="product-popup__top">
                          <div class="product-popup__img-container">
                              <img src="${foundElement.img}" alt="product">
                          </div>
                          <h2 class="title title--h2 product-popup__title">${foundElement.name}</h2>
                      </div>
                      <div class="product-popup__categories"></div>
                    </div>
                </div>
            `);

              let toCallPopup = document.querySelector('.call-popup')

              setTimeout(() => {
                toCallPopup.classList.add('product-popup__opened');
                toCallPopup.insertAdjacentHTML('afterend',`<div class="dark-block"></div>`);
                document.body.style.overflow = "hidden"
              },100)

              initBtnSettings(toCallPopup);
              inputsFunc(toCallPopup)


              let btnClose = toCallPopup.querySelector('.product-popup__close');
              let closePopupCall = () => {
                toCallPopup.classList.add('product-popup__closed')
                setTimeout(() => {
                  toCallPopup.remove();
                  document.querySelector('.dark-block')?.remove();
                  document.body.style.overflowY = "auto"
                },100)
              }

              if (btnClose){
                btnClose.addEventListener('click',() => {
                  closePopupCall()
                })
              }


              document.addEventListener('click', (e) => {
                if (e.target === document.querySelector('.dark-block')){
                  closePopupCall()
                }
              })

              if (foundElement.categories){
                foundElement.categories.forEach((category, index) => {
                  document.querySelector('.product-popup__categories').insertAdjacentHTML('beforeend', `
                    <div class="product-popup__menu" data-id="${index}">
                      <a href="${category.link}" class="product-popup__category-item">
                          <div class="product-popup__category-img">
                              ${category.svg}
                          </div>
                          <div class="product-popup__category">${category.name}</div>
                      </a>
                    </div>

                  `)
                  if (category.subcategories){
                    document.getElementsByClassName('product-popup__menu')[index].classList.add('product-popup__menu--with-subcategories')
                    document.getElementsByClassName('product-popup__menu')[index].insertAdjacentHTML('beforeend', `
                       <div class="product-popup__subcategories"></div>
                      `)
                  }
                  if (category.subcategories){
                    category.subcategories.forEach((subcategory,i) => {

                      document.getElementsByClassName('product-popup__menu')[index].querySelector('.product-popup__subcategories').insertAdjacentHTML('beforeend', `
                      <div class="product-popup__subcategory-container">
                        <div class="product-popup__subcategory-item">
                          <div class="product-popup__subcategory-img">
                              ${subcategory.svg}
                          </div>
                          <a class="product-popup__subcategory" href="${subcategory.link ? subcategory.link : '#'}">${subcategory.name}</a>
                        </div>
                        <div class="product-popup__products"></div>
                      </div>

                    `)


                      if (subcategory.products){
                        document.getElementsByClassName('product-popup__menu')[index].getElementsByClassName('product-popup__subcategory-item')[i].insertAdjacentHTML('beforeend',`
                            <img class="product-popup__dropdown-icon" src="images/production-catalog/svg/arrow.svg" alt="dropdown">
                          `)
                        subcategory.products.forEach(product => {
                          document.getElementsByClassName('product-popup__menu')[index].getElementsByClassName('product-popup__subcategory-container')[i].classList.add('product-popup__subcategory-container--with-products');
                          document.getElementsByClassName('product-popup__menu')[index].getElementsByClassName('product-popup__subcategory')[i].classList.add('product-popup__subcategory--with-products');

                          document.getElementsByClassName('product-popup__menu')[index].getElementsByClassName('product-popup__products')[i].insertAdjacentHTML('beforeend', `
                            <a href="${product.link}" class="product-popup__product">${product.name}</a>
                          `)
                        })
                      }
                    })
                  }
                })
              }
              changeWithBlock()
              dropdownFunc()
            })
          })
        }
      })}
})





