document.addEventListener('DOMContentLoaded', () => {
  let bannerImg = document.querySelector('.banner-img-wrp')
  let bannerMap = document.querySelector('.banner--map')
  if (bannerImg){
    if ((bannerImg && bannerImg.getAttribute('data-theme')) || (bannerMap && bannerMap.getAttribute('data-theme') )){
      document.body.classList.add('light-theme')
    } else {
      document.body.classList.remove('light-theme')
    }
  }
})
