document.addEventListener('DOMContentLoaded', () => {
  let filters = document.querySelectorAll('.filter-js');
  if (filters){
    filters.forEach(filter => {
      filter.addEventListener('click',() => {
        filter.classList.toggle('info-block__filters-item--active')
      })
    })
  }
  let resetBtn = document.querySelector('.info-block__btn-delete-filters');
  if (resetBtn){
    resetBtn.addEventListener('click', () => {
      filters.forEach(filter => {
        if (filter.classList.contains('info-block__filters-item--active')){
          filter.classList.remove('info-block__filters-item--active')
        }
      })
    })
  }
})



