document.addEventListener('DOMContentLoaded', () => {
  let kindsNav = document.querySelector('.kinds__navigation');
  if (kindsNav){
    kindsNav.querySelector('.kinds__arrow-next').addEventListener('click',() => {
      const ele = document.querySelector('.kinds__wrp');
        if (ele.scrollLeft <= 1000){
          ele.scrollLeft += 100;
        }
    })
    kindsNav.querySelector('.kinds__arrow-prev').addEventListener('click',() => {
      const ele = document.querySelector('.kinds__wrp');
      if (ele.scrollLeft > 0){
        ele.scrollLeft -= 100
      }

    })
  }
})




