import {toOrderACall} from "./toOrderACall-popup";
let menuBtns = document.querySelectorAll('.header__menu-mobile');
menuBtns.forEach(el => {
  el.addEventListener('click', () => {
    let mobileMenu = document.querySelector('.mobile-menu');
    if (mobileMenu){
      setTimeout(() => {
        mobileMenu.classList.add('mobile-menu__opened');
        mobileMenu.insertAdjacentHTML('afterend',`<div class="dark-block"></div>`);
        document.body.style.overflow = "hidden";

      },100)

      setTimeout(() => {

        if (window.innerWidth > 450){
          mobileMenu.querySelector('.mobile-menu__close').style.position = "fixed"
          mobileMenu.querySelector('.mobile-menu__close').style.right = "0"
          mobileMenu.querySelector('.mobile-menu__close').style.left = "400px"
        } else {
          mobileMenu.querySelector('.mobile-menu__close').style.position = "absolute"
          mobileMenu.querySelector('.mobile-menu__close').style.right = "7px"
          mobileMenu.querySelector('.mobile-menu__close').style.left = "initial"
        }

      }, 500)
    }
    let accordions = document.querySelectorAll('.accordion');
    if (accordions){
      accordions.forEach((el) => {
        el.addEventListener('click', () => {
          el.classList.toggle('active')
          el.querySelector('.mobile-menu__accordion-arrow').classList.toggle('mobile-menu__accordion-arrow-active')
          let panel = el.querySelector('.panel');
          if (panel.style.maxHeight) {
            panel.style.maxHeight = null;
          } else {
            panel.style.maxHeight = panel.scrollHeight + "px";
          }
          if (el.parentElement.querySelectorAll('.active').length > 1){
            el.parentElement.querySelectorAll('.active').forEach(item => {
              if (item !== el){
                item.querySelector('.panel').style.maxHeight = null;
                item.classList.remove('active');
                item.querySelector('.mobile-menu__accordion-arrow').classList.remove('mobile-menu__accordion-arrow-active')
              }
            })
          }
        })
      })
    }

    let btnClose = mobileMenu.querySelector('.mobile-menu__close');
    let closeMenu = () => {
      mobileMenu.querySelector('.mobile-menu__close').style.position = "absolute"
      mobileMenu.querySelector('.mobile-menu__close').style.left = "initial"
      mobileMenu.classList.add('mobile-menu__closed')
      setTimeout(() => {
        document.body.style.overflow = "auto";
        document.querySelector('.dark-block').remove();
        mobileMenu.classList.remove('mobile-menu__opened')
        mobileMenu.classList.remove('mobile-menu__closed');
      },100)
    }

    if (btnClose){
      btnClose.addEventListener('click',() => {
        closeMenu()
      })
    }

    document.addEventListener('click', (e) => {
      if (e.target === document.querySelector('.dark-block')){
        closeMenu()
      }
    })

    let btnCall = mobileMenu.querySelector('.mobile-menu__btn');
    if (btnCall){
      btnCall.addEventListener('click',toOrderACall);
    }
  })
})





