document.addEventListener("scroll", function(){
  let fixHeader = document.querySelector(".fix-header");
  if (window.scrollY >= 200){
    fixHeader.classList.add('fix-header__opened')
  } else {
    fixHeader.classList.remove('fix-header__opened')
  }
});

let searchBtns = document.querySelectorAll('.search-btn-js');
if (searchBtns){
  searchBtns.forEach(el => {
    el.addEventListener('click', () => {
      document.querySelector('.fix-search').classList.toggle('fix-search__opened')
      let closeBtn = document.querySelector('.fix-search__close');
      closeBtn.addEventListener('click', () => {
        document.querySelector('.fix-search').classList.remove('fix-search__opened');
        setTimeout(() => {
          document.querySelector('.fix-search').classList.remove('fix-search__opened')
        }, 500)
      })
    })
  })
}
