import {addBlocks, addSwiperSettings, closePopupSlider, hideBlocks} from "./slider-popup";

document.addEventListener('DOMContentLoaded', () => {
  let reviewBtns = document.querySelectorAll('.button-review-js');
  if (reviewBtns){
    reviewBtns.forEach(btn => {
      btn.addEventListener('click', (e) => {
        if (!e.target.classList.contains('partners-card__link')){
          let img = btn.getAttribute('data-img');
          let date = btn.getAttribute('data-date');
          let title = btn.getAttribute('data-title')

          document.querySelector('body').insertAdjacentHTML('afterbegin',
            `<div class="slider-popup">
                        <div class="swiper-container media-block__slider slider-popup__slider">
                            <div class="swiper-wrapper">
                              <div class="swiper-slide slide-js">
                                  <div class="swiper-zoom-container" data-swiper-zoom="3">
                                      <img src="${img}">
                                  </div>
                                  <div class="slide-data">
                                      <div class="slide-date">${date}</div>
                                      <div class="slide-name">${title}</div>
                                  </div>
                                </div>
                            </div>
                        </div>
                    </div>`
          )
          addSwiperSettings();
          addBlocks();
          setTimeout(() => {
            closePopupSlider();
          }, 100)
          setTimeout(() => {
            hideBlocks();
          }, 100)
        }
      })
    })
  }
})




