document.addEventListener('DOMContentLoaded', () => {
  let btnFilters = document.querySelector('.news-page__filters-mobile');
  if (btnFilters){
    btnFilters.addEventListener('click', () => {
      let popup = document.querySelector('.filters-mobile-popup');
      popup.classList.toggle('filters-mobile-popup--opened');
      popup.insertAdjacentHTML('afterend',`<div class="dark-block"></div>`);
      document.body.style.overflow = "hidden";

      let closePopup = () => {
        popup.classList.add('filters-mobile-popup--closed')
        setTimeout(() => {
          document.body.style.overflow = "auto";
          document.querySelector('.dark-block').remove();
          popup.classList.remove('filters-mobile-popup--opened')
          popup.classList.remove('filters-mobile-popup--closed')
        },100)
      }

      let btnClose = popup.querySelector('.filters-mobile-popup__close-btn');
      btnClose.addEventListener('click', () => {
        closePopup()
      })


      document.addEventListener('click', (e) => {
        if (e.target === document.querySelector('.dark-block')){
          closePopup()
        }
      })
    })
  }
})
