document.addEventListener('DOMContentLoaded', () => {
  let fixManager = document.querySelector(".col-js");
  if (fixManager){
    if (!document.querySelector('.banner--slider')){
      fixManager.classList.add('manager-block__initial2')
    }
    scrollManager()
  }
})

const scrollManager = () => {
  let fixManager = document.querySelector(".col-js");
  if (fixManager){
    let managerP = fixManager.getBoundingClientRect();
    let newsMore = document.querySelector(".news-more-page__main");
    let newsMorePos = newsMore.getBoundingClientRect();


    if(managerP.height + 60 >= newsMorePos.height)
      return;

    let top = -20;
    let topManager = 20;
    if(window.innerWidth > 1350){
      top = 50;
      topManager = 100
    }

    if(fixManager.classList.contains("manager-block__initial2"))
      if(window.innerWidth > 1350)
        top = 160;
      else
        top = 130;



    if(newsMorePos.top <= top){
      if(managerP.bottom+1 >= newsMorePos.bottom && managerP.top <= topManager) {
        fixManager.classList.add("js__bottomFixed");
        fixManager.classList.remove("js__fixed");
      }else{
        fixManager.classList.add("js__fixed");
        fixManager.classList.remove("js__bottomFixed");
      }
    }else{
      fixManager.classList.remove("js__fixed");
      fixManager.classList.remove("js__bottomFixed");
    }
  }

}
document.addEventListener("scroll", scrollManager);
